<template>
    <div>
        <iv-visualisation :title="pageName" >
            
            <template #hotspots>

                <iv-toggle-hotspot position="right" title="Sliders" style="width: 30vw; height: 50vh; z-index: 2;">
                    <div style="width:100%;">
                        <iv-toggle-advance :modes=modeNames @toggleswitched="toggleChange" position="centre"></iv-toggle-advance>

                        <div id="cylinSliders" v-if="tabSelected == 0" >
                            <label for="rhoslider" style="border-left: solid 5px rgb(219,209,0); padding-left: 8px;" > ρ-coordinate (index): </label>
                            <iv-slider id="rhoslider" ref="rhoController" @sliderChanged="change_rho" theme="Lime" :min="2" :max="4" :init_val="2" :step="0.2" :tick_step="0.5" />
                            
                            <label for="phislider" style="border-left: solid 5px rgb(182,109,255); padding-left: 8px;"> φ-coordinate (middle): </label>
                            <iv-slider id="phislider" ref="phiController" @sliderChanged="change_phi" theme="Lime" :min="0" :max="360" :init_val="0" :step="30" :tick_step="60" />
                            
                            <label for="zslider" style="border-left: solid 5px rgb(0,146,146); padding-left: 8px;"> z-coordinate (thumb): </label>
                            <iv-slider id="zslider" ref="z1Controller" @sliderChanged="change_z" theme="Lime" :min="0" :max="4" :init_val="1" :step="0.2" :tick_step="0.5" />
                        </div>
                    
                        <div id="sphereSliders" v-else>
                            
                            <label for="rslider" style="border-left: solid 5px rgb(0,146,146); padding-left: 8px;"> r-coordinate (thumb): </label>
                            <iv-slider id="rslider" ref="rController" @sliderChanged="change_r" theme="Lime" :min="4" :max="5.2" :init_val="4" :step="0.1" :tick_step="0.5" />
                            
                            <label for="thetaslider" style="border-left: solid 5px rgb(219,209,0); padding-left: 8px;"> θ-coordinate (index): </label>
                            <iv-slider id="thetaslider" ref="thetaController" @sliderChanged="change_theta" theme="Lime" :min="0" :max="180" :init_val="45" :step="30" :tick_step="30" />
                            
                            <label for="phi2slider" style="border-left: solid 5px rgb(182,109,255); padding-left: 8px;"> φ-coordinate (middle): </label>
                            <iv-slider id="phi2slider" ref="phi2Controller" @sliderChanged="change_phi2" theme="Lime" :min="0" :max="360" :init_val="0" :step="30" :tick_step="60" />

                        
                        </div>    
                    
                    </div>

                    
                </iv-toggle-hotspot>

                <iv-pane position="left" format="push">
                    <iv-sidebar-content :showPagination="false">
                        <iv-sidebar-section title="Instructions">
                            <p>
                            Try moving the sliders to change the coordinates of the volume element. What do you notice? 
                            </p>
                            <img src='asset/RHR.png' width=150 height=150>
                            <p>
                            In what circumstances might we favour using spherical coordinates over cylindrical coordinates, and vice versa?
                            </p>
                        </iv-sidebar-section>
                    </iv-sidebar-content>
                </iv-pane>



            </template>
                
            <div class="iv-welcome-message">
                <h3>Volume element:</h3>
                <div id='graph' style="width:450px; height:450px;"></div>    
            </div>  
                       

        </iv-visualisation>
    </div>
</template>
<script>
//import vue_config from '../../vue.config.js';
import Plotly from 'plotly.js/dist/plotly.min.js';
//import Plotly from 'plotly.js';
import $ from 'jquery';
import numeric from 'numeric';
//import math from 'math';

export default {
    name:"cylindrical",
    data(){
        return {
            pageName:"Volume Elements in Curvilinear Coordinate Systems",
            //vue_config,

            rho_val: this.init_rho,
            phi_val: this.init_phi,
            z_val: this.init_z,

            r_val:4,
            phi2_val:0,
            theta_val:45,

            sliderupdate:this.init_slider1,
            
            tabSelected:0

        }
    },

    props:{
            init_rho:{default:3},
            init_phi:{default:0},
            init_z:{default:1},
            init_slider1:{default:false},
            modeNames:{default: ["Cylindrical", "Spherical"]}
            
    },
    methods: {
        change_rho: function(val) {
            this.rho_val = val;
            this.sliderupdate = true;
            },
        
        change_phi: function(val) {
            this.phi_val = val;
            this.sliderupdate = true;
            },
        change_z: function(val) {
            this.z_val = val;
            this.sliderupdate = true;
            },

        change_r: function(val) {
            this.r_val = val;
            this.sliderupdate = true;
            },
        
        change_phi2: function(val) {
            this.phi2_val = val;
            this.sliderupdate = true;
            },
        change_theta: function(val) {
            this.theta_val = val;
            this.sliderupdate = true;
            },
                

        toggleChange(e){
            this.tabSelected = e;
            this.sliderupdate = true;
        }

    },

    mounted() {
        let v = this;
        
        
        var cyan = "rgb(0,146,146)", //1
            lilac = "rgb(182,109,255)", //2
            orange = "rgb(219,209,0)"; //3

        // Plot Utilities:
        /**
         * sets camera options in layout.
         * @param {array} point - point of viewpoint.
         */
        
        /**
         * Creates Axes Object.
         * @param {float} length - length of the half axes.
         */
        function createAxes(length) {
            var axes = [];
            var vector = [0, length];
            var initialVec;
            var vecString = ["x", "y", "z"];
            var originString = ["", "", "(0,0,0)"];
            for (let i = 0; i < 3; ++i) {
                initialVec = [[0, 0], [0, 0], [0, 0]];
                initialVec[i] = vector;
                axes.push(
                    {
                        type: "scatter3d",
                        mode: "lines+text",
                        x: initialVec[0],
                        y: initialVec[1],
                        z: initialVec[2],
                        line: {color: "rgb(0,0,0)", width: 4},
                        text: [originString[i], vecString[i]],
                        textfont: {size: 15},
                        textposition: "top"
                    }
                );
            }
            return axes;
        }

        // Change of Basis:
        /**
         * changes spherical to cartesian coordinates
         * @param {float} r - r.
         * @param {float} theta - theta.
         * @param {float} phi - phi.
         */
        
        /**
         * changes cartesian to spherical coordinates
         * @param {float} x - x.
         * @param {float} y - y.
         * @param {float} z - z.
         */
        
        /**fti
         * changes polar to cartesian coordinates
         * @param {float} rho - rho.
         * @param {float} phi - phi.
         */
        // function p2c(rho, phi) {
        //     return [
        //         rho * Math.cos(phi),
        //         rho * Math.sin(phi)
        //     ];
        // }
        /**
         * changes cartesian to polar coordinates
         * @param {float} x - x.
         * @param {float} y - y.
         */
        


        ///Shape Objects:
        //3D Objects:
        /**
         * Represents a line.
         * @constructor
         * @param {list} points - list of the points in the line.
         */
        function Line(points) {
            this.x = [];
            this.y = [];
            this.z = [];
            for (let i = 0; i  < points.length; ++i) {
                this.x.push(points[i][0]);
                this.y.push(points[i][1]);
                this.z.push(points[i][2]);
            }

            this.gObject = function(color, width=7, dash="solid") {
                var lineObject = {
                    type: "scatter3d",
                    mode: "lines",
                    x: this.x,
                    y: this.y,
                    z: this.z,
                    line: {color: color, width: width, dash:dash}
                }
                return lineObject;
            }

            
        }
        
       

      
        //Global Initial Parameters:
        var initialPoint = [3.4, 1.4, 3.7];
        var layout = {
            width: 450,
            height: 500,
            margin: { l: 0, r: 0, t: 0, b: 0 },
            hovermode: "closest",
            showlegend: false,
            scene: {
                camera: {
                    up: { x: 0, y: 0, z: 1 },
                    eye: { x: 1.1, y: -0.2, z: 0.4 },
                    center: { x: 0, y: 0, z: 0 },
                },
                xaxis: { range: [-6, 6], zeroline: true, scaleratio: 1 },
                yaxis: { range: [-6, 6], zeroline: true, scaleratio: 1 },
                zaxis: { range: [-6, 6], zeroline: true, scaleratio: 1 },
                aspectratio: { x: 1, y: 1, z: 1 },
            },
        };
        var currentPoint = initialPoint;
        let initialRho = 0,
            initialPhi = 0;
            
        var axes = createAxes(5);
        
        var blackTextType = "lines+text";

        /**
         * It adds more curvatures to multiply to the curve array.
         * @param {float} arraySize - size of the curve array.
         */
        function curveMore(arraySize) {
            var hello = [],
                hello2;
            if (arraySize % 2 === 0) {
                for (let i = 0, n = Math.round(arraySize / 2); i < n; ++i) {
                    hello.push(1 + i / n / 100);
                }
                hello2 = hello.slice();
            } else {
                for (let i = 0, n = Math.round(arraySize / 2) - 1; i < n; ++i) {
                    hello.push(1 + i / n / 100);
                }
                hello2 = hello.slice();
                hello.push(1.008);
            }

            return hello.concat(hello2.reverse());
        }

        //Plot
        /**
         * Resets and plots initial volume element.
         * // * [at symbol] param {string} coor - type of coordinate systems
         */
        function initRiley() {
            
            Plotly.purge("graph");
            
            //if (coor === "#cylin") {
                initialRho = Math.sqrt(
                    currentPoint[0] * currentPoint[0] +
                        currentPoint[1] * currentPoint[1]
                );
                initialPhi =
                    ((Math.atan2(currentPoint[1], currentPoint[0]) + 2 * Math.PI) %
                        (2 * Math.PI)) /
                    Math.PI;
                initialRho = Math.round(initialRho * 10) / 10;
                initialPhi = Math.round(initialPhi * 8) / 8;


                //COMMENTED OUT AS A TEST 
                // $("#rhoController").val(initialRho);
                // $("#rhoControllerDisplay").text(initialRho);
                // $("#phiController").val(initialPhi);
                // $("#phiControllerDisplayA1").text(
                //     "(" +
                //         initialPhi * 8 +
                //         "/8)" +
                //         $("#phiControllerDisplayA1").attr("data-unit")
                // );
                // $("#phiControllerDisplayA2").text(
                //     initialPhi * 180 + $("#phiControllerDisplayA2").attr("data-unit")
                // );
                // $("#z1Controller").val(currentPoint[2]);
                // $("#z1ControllerDisplay").text(currentPoint[2]);


                var rho = v.rho_val;
                var phi = v.phi_val * Math.PI /180;
                var z = v.z_val;

                Plotly.newPlot("graph", computeCylindrical(rho, phi, z), layout);

            
            return;
        }
        /**
         * Computes the volume element of the cylindrical coordinate system.
         * @param {float} rho - rho value.
         * @param {float} phi - phi value.
         * @param {float} z - z value.
         */
        
        function computeCylindrical(rho, phi, z) {
            var x0 = rho * Math.cos(phi);
            var y0 = rho * Math.sin(phi);
            var z0 = z;
            currentPoint = [
                Math.round(x0 * 10) / 10,
                Math.round(y0 * 10) / 10,
                Math.round(z0 * 10) / 10,
            ];

            var newPhi = phi + Math.PI / 16;

            var x1 = rho * Math.cos(newPhi);
            var y1 = rho * Math.sin(newPhi);
            var z1 = z + 0.8;

            var meshSize0 = Math.round((phi / Math.PI) * 24);

            var intermediatePhi0 = numeric.linspace(0, phi, meshSize0);
            var xPhi0 = [],
                yPhi0 = [];
            for (var i = 0; i < meshSize0; ++i) {
                xPhi0[i] = 0.3 * rho * Math.cos(intermediatePhi0[i]);
                yPhi0[i] = 0.3 * rho * Math.sin(intermediatePhi0[i]);
            }
            var zPhi0 = numeric.rep([meshSize0], 0);

            var meshSize1 = 9;
            var curved = curveMore(meshSize1);

            var intermediatePhi1 = numeric.linspace(phi, newPhi, meshSize1);
            var xPhi1 = [],
                yPhi1 = [],
                zPhi1 = numeric.rep([meshSize1], 0);
            var xPhi2 = [],
                yPhi2 = [],
                zPhi2 = numeric.rep([meshSize1], z0);
            var xPhi3 = [],
                yPhi3 = [];
            for (let i = 0; i < meshSize1; ++i) {
                xPhi1[i] = 0.45 * rho * Math.cos(intermediatePhi1[i]);
                yPhi1[i] = 0.45 * rho * Math.sin(intermediatePhi1[i]);
                xPhi2[i] = curved[i] * rho * Math.cos(intermediatePhi1[i]);
                yPhi2[i] = curved[i] * rho * Math.sin(intermediatePhi1[i]);
                xPhi3[i] = curved[i] * (rho + 0.8) * Math.cos(intermediatePhi1[i]);
                yPhi3[i] = curved[i] * (rho + 0.8) * Math.sin(intermediatePhi1[i]);
            }

            var xTrace = xPhi2.concat(xPhi3.reverse());
            xTrace.push(xPhi2[0]);
            var yTrace = yPhi2.concat(yPhi3.reverse());
            yTrace.push(yPhi2[0]);

            var intermediateZ = numeric.linspace(z0, z1, 2 * meshSize1 + 1);

            var zTrace = [],
                xTrace1 = [],
                yTrace1 = [];

            for (let i = 0, n = intermediateZ.length; i < n; ++i) {
                xTrace1.push(xTrace);
                yTrace1.push(yTrace);
                zTrace.push(numeric.rep([2 * meshSize1 + 1], intermediateZ[i]));
            }

            var phiText = [];
            for (let i = 0; i < meshSize0; ++i) {
                phiText.push("");
            }
            phiText[Math.round(meshSize0 / 2)] = "φ";

            var emptyText = [];
            for (let i = 0; i < meshSize1; ++i) {
                emptyText.push("");
            }

            var dphiText = emptyText.slice();
            dphiText[Math.round(meshSize1 / 2)] = "dφ";

            var rhodphiText = emptyText.slice();
            rhodphiText[Math.round(meshSize1 / 2)] = "ρ dφ";

            var data = [
                axes[0],
                axes[1],
                axes[2],
                {
                    type: "scatter3d",
                    mode: "lines+text",
                    x: [x0, x0 + 0.5 * Math.cos(phi), x0 + 0.8 * Math.cos(phi)],
                    y: [y0, y0 + 0.5 * Math.sin(phi), y0 + 0.8 * Math.sin(phi)],
                    z: [z0, z0, z0],
                    line: { color: orange, width: 7, dash: "solid" },
                    text: ["", "dρ", ""],
                    //textposition: "top left",
                    textfont: { color: orange, size: 18 },
                },
                {
                    type: "scatter3d",
                    mode: "lines+text",
                    x: xPhi2,
                    y: yPhi2,
                    z: zPhi2,
                    line: { color: lilac, width: 7, dash: "solid" },
                    text: rhodphiText,
                    //textposition: "top",
                    textfont: { color: lilac, size: 18 },
                },
                {
                    type: "scatter3d",
                    mode: "lines+text",
                    x: [x0, x0, x0],
                    y: [y0, y0, y0],
                    z: [z0, z0 + 0.4, z1],
                    line: { color: cyan, width: 7, dash: "solid" },
                    text: ["", "dz", ""],
                    //textposition: "left",
                    textfont: { color: cyan, size: 18 },
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: [0, (3 * x0) / 5, x0],
                    y: [0, (3 * y0) / 5, y0],
                    z: [0, 0, 0],
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: ["", "ρ", ""],
                    textfont: { size: 16 },
                    //textposiition: "bottom"
                },
                new Line([
                    [0, 0, 0],
                    [x1, y1, 0],
                ]).gObject("rgb(0,0,0)", 2),
                new Line([
                    [x0, y0, 0],
                    [x0, y0, z],
                ]).gObject("rgb(0,0,0)", 2, "longdash"),
                new Line([
                    [x1, y1, 0],
                    [x1, y1, z],
                ]).gObject("rgb(0,0,0)", 2, "longdash"),
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xPhi0,
                    y: yPhi0,
                    z: zPhi0,
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: phiText,
                    textfont: { size: 16 },
                    //textposition: "bottom"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xPhi1,
                    y: yPhi1,
                    z: zPhi1,
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: dphiText,
                    textfont: { size: 16 },
                    //textposition: "top"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xPhi2,
                    y: yPhi2,
                    z: zPhi1,
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: rhodphiText,
                    textfont: { size: 16 },
                    //textposition: "bottom"
                },
                {
                    type: "scatter3d",
                    mode: "lines",
                    x: xTrace,
                    y: yTrace,
                    z: zTrace[0],
                    line: { color: "rgb(0,62,116)", width: 2 },
                    surfaceaxis: 2,
                    opacity: 0.2,
                },
                {
                    type: "scatter3d",
                    mode: "lines",
                    x: xTrace,
                    y: yTrace,
                    z: zTrace[zTrace.length - 1],
                    line: { color: "rgb(0,62,116)", width: 2 },
                    surfaceaxis: 2,
                    opacity: 0.2,
                },
                {
                    type: "surface",
                    x: xTrace1,
                    y: yTrace1,
                    z: zTrace,
                    showscale: false,
                    opacity: 0.4,
                    colorscale: [
                        [0.0, "rgb(0,62,116)"],
                        [1.0, "rgb(255,255,255)"],
                    ],
                },
            ];

            return data;
        }
        /**
         * Computes the volumne element of the spherical coordinate system.
         * @param {float} r - rho value.
         * @param {float} theta - theta value.
         * @param {float} phi - phi value.
         */
        function computeSpherical(r, theta, phi) {
            var x0 = r * Math.cos(phi) * Math.sin(theta);
            var y0 = r * Math.sin(phi) * Math.sin(theta);
            var z0 = r * Math.cos(theta);
            // console.log("here!")
            // console.log(phi)
            // console.log(theta)
            currentPoint = [
                Math.round(x0 * 10) / 10,
                Math.round(y0 * 10) / 10,
                Math.round(z0 * 10) / 10,
            ];

            var newPhi = phi + Math.PI / 16;
            var newTheta = theta + (3 * Math.PI) / 64;

            var x1 = r * Math.cos(newPhi) * Math.sin(theta);
            var y1 = r * Math.sin(newPhi) * Math.sin(theta);
            //let z1 = r * Math.cos(theta);

            var x2 = r * Math.cos(phi) * Math.sin(newTheta);
            var y2 = r * Math.sin(phi) * Math.sin(newTheta);
            var z2 = r * Math.cos(newTheta);

            var meshSize0 = Math.round((phi / Math.PI) * 24);

            var intermediatePhi0 = numeric.linspace(0, phi, meshSize0);
            var xPhi0 = [],
                yPhi0 = [];
            for (let i = 0; i < meshSize0; ++i) {
                xPhi0[i] = 0.3 * r * Math.cos(intermediatePhi0[i]) * Math.sin(theta);
                yPhi0[i] = 0.3 * r * Math.sin(intermediatePhi0[i]) * Math.sin(theta);
            }
            var zPhi0 = numeric.rep([meshSize0], 0);

            var meshSize1 = 9;
            var curved = curveMore(meshSize1);

            var intermediatePhi1 = numeric.linspace(phi, newPhi, meshSize1);
            var xPhi1 = [],
                yPhi1 = [],
                zPhi1 = numeric.rep([meshSize1], 0);
            var xPhi2 = [],
                yPhi2 = [],
                zPhi2 = numeric.rep([meshSize1], z0);
            for (let i = 0; i < meshSize1; ++i) {
                xPhi1[i] = 0.45 * r * Math.cos(intermediatePhi1[i]) * Math.sin(theta);
                yPhi1[i] = 0.45 * r * Math.sin(intermediatePhi1[i]) * Math.sin(theta);
                xPhi2[i] =
                    curved[i] * r * Math.cos(intermediatePhi1[i]) * Math.sin(theta);
                yPhi2[i] =
                    curved[i] * r * Math.sin(intermediatePhi1[i]) * Math.sin(theta);
            }

            var meshSize2 = Math.round((theta / Math.PI) * 24);
            var intermediateTheta0 = numeric.linspace(0, theta, meshSize2);
            var xTheta0 = [],
                yTheta0 = [],
                zTheta0 = [];
            for (let i = 0; i < meshSize2; ++i) {
                xTheta0[i] = 0.3 * r * Math.cos(phi) * Math.sin(intermediateTheta0[i]);
                yTheta0[i] = 0.3 * r * Math.sin(phi) * Math.sin(intermediateTheta0[i]);
                zTheta0[i] = 0.3 * r * Math.cos(intermediateTheta0[i]);
            }

            var intermediateTheta1 = numeric.linspace(theta, newTheta, meshSize1);
            var xTheta1 = [],
                yTheta1 = [],
                zTheta1 = [];
            var xTheta2 = [],
                yTheta2 = [];
            var zTemp1 = [],
                zTemp2 = [];
            for (let i = 0; i < meshSize1; ++i) {
                xTheta1[i] = 0.45 * r * Math.cos(phi) * Math.sin(intermediateTheta1[i]);
                yTheta1[i] = 0.45 * r * Math.sin(phi) * Math.sin(intermediateTheta1[i]);
                zTheta1[i] = 0.45 * r * Math.cos(intermediateTheta1[i]);
                xTheta2[i] =
                    curved[i] * r * Math.cos(phi) * Math.sin(intermediateTheta1[i]);
                yTheta2[i] =
                    curved[i] * r * Math.sin(phi) * Math.sin(intermediateTheta1[i]);
                zTemp1[i] = curved[i] * r * Math.cos(intermediateTheta1[i]);
                zTemp2[i] = curved[i] * (r + 0.8) * Math.cos(intermediateTheta1[i]);
            }

            var xTrace = [],
                yTrace = [],
                zTrace = [];

            var xTemp1, yTemp1, xTemp2, yTemp2;
            zTemp2.reverse();

            for (let i = 0; i < meshSize1; ++i) {
                (xTemp1 = []), (xTemp2 = []), (yTemp1 = []), (yTemp2 = []);
                for (var j = 0; j < meshSize1; ++j) {
                    xTemp1[j] =
                        curved[j] *
                        r *
                        Math.cos(intermediatePhi1[i]) *
                        Math.sin(intermediateTheta1[j]);
                    yTemp1[j] =
                        curved[j] *
                        r *
                        Math.sin(intermediatePhi1[i]) *
                        Math.sin(intermediateTheta1[j]);
                    xTemp2[j] =
                        curved[j] *
                        (r + 0.8) *
                        Math.cos(intermediatePhi1[i]) *
                        Math.sin(intermediateTheta1[j]);
                    yTemp2[j] =
                        curved[j] *
                        (r + 0.8) *
                        Math.sin(intermediatePhi1[i]) *
                        Math.sin(intermediateTheta1[j]);
                }
                xTrace[i] = xTemp1.concat(xTemp2.reverse());
                xTrace[i].push(xTemp1[0]);
                yTrace[i] = yTemp1.concat(yTemp2.reverse());
                yTrace[i].push(yTemp1[0]);
                zTrace[i] = zTemp1.concat(zTemp2);
                zTrace[i].push(zTemp1[0]);
            }

            var phiText = [];
            for (let i = 0; i < meshSize0; ++i) {
                phiText.push("");
            }
            phiText[Math.round(meshSize0 / 2)] = "φ";

            var thetaText = [];
            for (let i = 0; i < meshSize2; ++i) {
                thetaText.push("");
            }
            thetaText[Math.round(meshSize2 / 2)] = "θ";

            var emptyText = [];
            for (let i = 0; i < meshSize1; ++i) {
                emptyText.push("");
            }

            var dphiText = emptyText.slice();
            dphiText[Math.round(meshSize1 / 2)] = "dφ";

            var dthetaText = emptyText.slice();
            dthetaText[Math.round(meshSize1 / 2)] = "dθ";

            var rdthetaText = emptyText.slice();
            rdthetaText[Math.round(meshSize1 / 2)] = "r dθ";

            var rsinthetadphiText = emptyText.slice();
            rsinthetadphiText[Math.round(meshSize1 / 2)] = "r sin(θ) dφ";

            var data = [
                axes[0],
                axes[1],
                axes[2],
                {
                    type: "scatter3d",
                    mode: "lines+text",
                    x: [
                        x0,
                        x0 + 0.6 * Math.cos(phi) * Math.sin(theta),
                        x0 + 0.8 * Math.cos(phi) * Math.sin(theta),
                    ],
                    y: [
                        y0,
                        y0 + 0.6 * Math.sin(phi) * Math.sin(theta),
                        y0 + 0.8 * Math.sin(phi) * Math.sin(theta),
                    ],
                    z: [z0, z0 + 0.6 * Math.cos(theta), z0 + 0.8 * Math.cos(theta)],
                    line: { color: cyan, width: 7 },
                    text: ["", "dr", ""],
                    textfont: { color: cyan, size: 18 },
                    //textposition: "top center"
                },
                {
                    type: "scatter3d",
                    mode: "lines+text",
                    x: xPhi2,
                    y: yPhi2,
                    z: zPhi2,
                    line: { color: lilac, width: 7 },
                    text: rsinthetadphiText,
                    textfont: { color: lilac, size: 18 },
                    //textposition: "top"
                },
                {
                    type: "scatter3d",
                    mode: "lines+text",
                    x: xTheta2,
                    y: yTheta2,
                    z: zTemp1,
                    line: { color: orange, width: 7 },
                    text: rdthetaText,
                    textfont: { color: orange, size: 18 },
                    //textposition: "left"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: [0, (3 * x0) / 5, x0],
                    y: [0, (3 * y0) / 5, y0],
                    z: [0, 0, 0],
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: ["", "r sin(θ)", ""],
                    textfont: { size: 16 },
                    //textposition: "bottom"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: [0, (3 * x0) / 5, x0],
                    y: [0, (3 * y0) / 5, y0],
                    z: [z0, z0, z0],
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: ["(0, 0, r cos(θ))", "r sin(θ)", ""],
                    textfont: { size: 16 },
                    //textposition: "bottom"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: [0, 0.65 * x0, x0],
                    y: [0, 0.65 * y0, y0],
                    z: [0, 0.65 * z0, z0],
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: ["", "r", ""],
                    textfont: { size: 16 },
                    //textposition: "top"
                },
                new Line([
                    [0, 0, 0],
                    [x2, y2, z2],
                ]).gObject("rgb(0,0,0)", 2),
                new Line([
                    [0, 0, z0],
                    [x1, y1, z0],
                ]).gObject("rgb(0,0,0)", 2),
                new Line([
                    [0, 0, 0],
                    [x1, y1, 0],
                ]).gObject("rgb(0,0,0)", 2),
                new Line([
                    [x0, y0, 0],
                    [x0, y0, z0],
                ]).gObject("rgb(0,0,0)", 2, "longdash"),
                new Line([
                    [x1, y1, 0],
                    [x1, y1, z0],
                ]).gObject("rgb(0,0,0)", 2, "longdash"),
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xPhi0,
                    y: yPhi0,
                    z: zPhi0,
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: phiText,
                    textfont: { size: 16 },
                    //textposition: "bottom"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xPhi1,
                    y: yPhi1,
                    z: zPhi2,
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: dphiText,
                    textfont: { size: 16 },
                    //textposition: "top"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xPhi1,
                    y: yPhi1,
                    z: zPhi1,
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: dphiText,
                    textfont: { size: 16 },
                    //textposition: "top"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xPhi2,
                    y: yPhi2,
                    z: zPhi1,
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: rsinthetadphiText,
                    textfont: { size: 16 },
                    //textposition: "bottom"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xTheta0,
                    y: yTheta0,
                    z: zTheta0,
