                    text: thetaText,
                    textfont: { size: 16 },
                    //textposition: "top"
                },
                {
                    type: "scatter3d",
                    mode: blackTextType,
                    x: xTheta1,
                    y: yTheta1,
                    z: zTheta1,
                    line: { color: "rgb(0,0,0)", width: 2 },
                    text: dthetaText,
                    textfont: { size: 16 },
                    //textposition: "bottom"
                },
                {
                    type: "scatter3d",
                    mode: "lines",
                    x: xTrace[0],
                    y: yTrace[0],
                    z: zTrace[0],
                    line: { color: "rgb(0,62,116)", width: 2 },
                    surfaceaxis: 0,
                    opacity: 0.2,
                },
                {
                    type: "scatter3d",
                    mode: "lines",
                    x: xTrace[xTrace.length - 1],
                    y: yTrace[yTrace.length - 1],
                    z: zTrace[zTrace.length - 1],
                    line: { color: "rgb(0,62,116)", width: 2 },
                    surfaceaxis: 0,
                    opacity: 0.2,
                },
                {
                    type: "surface",
                    x: xTrace,
                    y: yTrace,
                    z: zTrace,
                    showscale: false,
                    opacity: 0.4,
                    colorscale: [
                        [0.0, "rgb(0,62,116)"],
                        [1.0, "rgb(255,255,255)"],
                    ],
                },
            ];

            return data;
        }

        /** updates the plot according to the slider controls. */
        function updatePlot() {
            var data = [];
            
            
            if (v.tabSelected==0) {
                let rho = v.rho_val;
                let phi = v.phi_val * Math.PI / 180;
                let z = v.z_val;
                data = computeCylindrical(rho, phi, z);
            } else {
                let r = v.r_val;
                let theta = v.theta_val * Math.PI/180;
                let phi = v.phi2_val * Math.PI/180;
                data = computeSpherical(r, theta, phi);
            }




            Plotly.animate(
                "graph",
                { data: data },
                {
                    fromcurrent: true,
                    transition: { duration: 0 },
                    frame: { duration: 0, redraw: true },
                    mode: "immediate",
                }
            );
        }

        function main() {
            

            $("input[type=checkbox]").each(function () {
                $(this).on("change", function () {
                    blackTextType = "lines+text";
                    initRiley();
                    
                });
            });

            $(function () {
                
                    initRiley();
                    return false;
                });

            initRiley();
        }
      
        function animate(){
            requestAnimationFrame(animate);

            if(v.sliderupdate==true){
                updatePlot();
                v.sliderupdate = false;
            }
            
        }

        main();
        animate();



    }


}
</script>
